import React, { useEffect, useState } from 'react';
import VisibilitySensor from 'react-visibility-sensor';
import './Stat.scss';
import './StatContainer.scss';

export interface StatProps {
    name: string;
    value: number;
    unit?: string;
}

export function Stat(props: StatProps) {

    const [value, setValue] = useState(0);
    const [wasVisible, setWasVisible] = useState(false);

    useEffect(() => {
        const fun = () => {
            let tmp = (1 - value * value) * 0.05 + 0.001;
            setValue(Math.min(value + tmp, 1));
        }

        if (wasVisible && value < 1) {
            var timeout = setTimeout(fun, 30);
        }

        return () => {
            clearTimeout(timeout);
        };
    }, [wasVisible, value]);

    const onVisibilityChange = (isVisible: boolean) => {
        if (isVisible && !wasVisible) {
            setWasVisible(true);
        }
    }

    return (
        <VisibilitySensor onChange={onVisibilityChange}>
            <div className="Stat">
                <div className="Stat__number">{Math.floor(value * props.value)} {props?.unit}</div>
                <div className="Stat__name">{props.name}</div>
            </div>
        </VisibilitySensor>
    );
}
