import * as THREE from 'three';
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader';
import { getFont } from '../../utils/loaders';
import pathSolar from '../../assets/models/pathSolar.obj';
import Roboto_Regular from '../../assets/fonts/Roboto_Regular.json';
import { CSS3DObject } from 'three/examples/jsm/renderers/CSS3DRenderer.js';
import img1 from "../../assets/img/test/0.jpg";

export interface PathData {
    type: string;
    x: number;
    z: number;
    rotation: number;
    color: number;
    flag: boolean;
    date: string;
    header: string;
    description: string;
    textShift: number;
}

function getModel(path: string) {
    let loader = new OBJLoader();
    let material = new THREE.MeshPhysicalMaterial({ color: 0x555555 });
    return new Promise((resolve, reject) => {
        loader.load(path, (object: THREE.Group) => {
            object.traverse(function (child) {
                if (child instanceof THREE.Mesh) {
                    child.material = material;
                }
            });
            resolve(object);
        }, undefined, (error) => {
            reject(error);
        });
    });
}


export async function createPathBuilder() {
    try {
        let [obj, font] = await Promise.all([
            getModel(pathSolar),
            getFont(Roboto_Regular)
        ])
        return new PathBuilder(obj as THREE.Group, font as THREE.Font);
    } catch (error) {
        console.error(error);
        throw error;
    };
}

export default class PathBuilder {
    private scale: number = 10;

    constructor(private object: THREE.Group, private font: THREE.Font) { }
    // 0 - straight
    // 1 - first part of flag
    // 3 - second part of flag
    // 4 - turn

    getStart(x: number, y: number) {
        let model = this.object?.children[0].clone() as THREE.Mesh;
        model.position.x = x * this.scale * 101;
        model.position.z = y * this.scale * 2;
        model.rotation.y = 90 / 180 * Math.PI;
        model.scale.setScalar(this.scale);
        model.scale.z = this.scale * 100;
        model.material = new THREE.MeshPhysicalMaterial({ color: 0x1488CC,opacity:0.5, reflectivity: 0.5, roughness: 0.5, metalness: 0.05, clearcoat: 0.5, side: THREE.DoubleSide });

        return model;
    }

    getObj(type: number, x: number, y: number, rotation: number, color: number) {
        let model = this.object?.children[type].clone() as THREE.Mesh;
        model.position.x = x * this.scale * 2;
        model.position.z = y * this.scale * 2;
        model.rotation.y = rotation / 180 * Math.PI;
        model.scale.setScalar(this.scale);
        model.material = new THREE.MeshPhysicalMaterial({ color: color, reflectivity: 0.5, roughness: 0.5, metalness: 0.05, clearcoat: 0.5, side: THREE.DoubleSide });

        return model;
    }

    getFlag(x: number, y: number, color: number) {
        let flag = this.getObj(1, x, y, 90, color);
        let stick = this.getObj(3, x, y, 0, 0xffffff);
        let parent = new THREE.Object3D();
        parent.add(flag);
        parent.add(stick);
        return parent;
    }

    getHTML(params: PathData, y: number) {
        let shift = params.textShift / 1.2;
        var elem = document.createElement("div");
        elem.className = "Timeline__label";
        elem.style.color =  "#" + params.color.toString(16);
        if (params.date !== '') {
            elem.innerHTML += `<h3 class="Timeline__label-date">${params.date}</h3>`;
        }
        if (params.header !== '') {
            elem.innerHTML += `<h4 class="Timeline__label-header">${params.header}</h3>`;
        }
        if (params.description !== "") {
            elem.innerHTML += `<p class="Timeline__label-description">${params.description}</p>`;
        }
        // elem.innerHTML += `<img src="${img1}" alt=""/>`
        var cssObject = new CSS3DObject( elem );
        cssObject.position.z = shift * this.scale * 2;
        cssObject.position.y = y * this.scale * 2;
        cssObject.rotation.y = 90 / 180 * Math.PI;
        cssObject.scale.setScalar(0.005 * this.scale);
        return cssObject;
    }

    getObject3d(params: PathData) {
        let obj = new THREE.Object3D();
        if (params.type === 'straight') {
            obj.add(this.getObj(0, 0, 0, params.rotation, params.color));
        }
        if (params.type === 'turn') {
            obj.add(this.getObj(4, 0, 0, params.rotation, params.color));
        }
        if (params.flag) {
            obj.add(this.getFlag(0, 0, params.color));
            obj.add(this.getHTML(params, 0.35))
            
        }
        obj.position.x = params.x * this.scale * 2;
        obj.position.z = params.z * this.scale * 2;
        return obj;
    }
}
