import { AmbientLight, Color, DirectionalLight, OrthographicCamera, Scene } from 'three';
import { ThreeSection } from '../context/threeSection';
import Particles from '../common/particles/Particles';
import { HomeSectionKey, HomePageConfig } from '../solarContext';
import { Timeline, TimelineConfig } from './timeline';
import { CSS3DRenderer } from 'three/examples/jsm/renderers/CSS3DRenderer.js';

export interface TimelineSectionConfig {
    timeline?: TimelineConfig;
}

export class TimelineSection implements ThreeSection<HomePageConfig, typeof HomeSectionKey.TIMELINE_SECTION> {
    public scene: Scene;
    public camera: OrthographicCamera;
    public timeline: Timeline;
    private particles: Particles;
    private progress: number = 0;

    private rendererCSS : CSS3DRenderer;

    public backgroundColor: Color = new Color(0x334488);
    public backgroundSpec = {
        primaryIndex: 6,
        secondaryIndex: 5,
        mix: 0,
    };

    private _sectionHeight: number = 0;
    private innerProgress: number = 0;

    constructor(public readonly key: typeof HomeSectionKey.TIMELINE_SECTION) {
        this.rendererCSS = new CSS3DRenderer();
        this.rendererCSS.setSize( window.innerWidth, window.innerHeight );
        this.rendererCSS.domElement.style.position = 'absolute';
        this.rendererCSS.domElement.style.top = '0px';
        this.rendererCSS.domElement.style.pointerEvents = "none";
        document.body.appendChild( this.rendererCSS.domElement );

        this.scene = new Scene();
        // this.scene.background = this.backgroundColor;
        this.camera = new OrthographicCamera( window.innerWidth / - 2, window.innerWidth / 2, window.innerHeight / 2, window.innerHeight / - 2, 0.5, 2000 );
        this.camera.zoom=17;
        this.camera.position.set(  85, 40, 30);
        this.camera.lookAt( 0,0,0 );

        var ambientLight = new AmbientLight(0xffffff, 0.2);
        this.scene.add(ambientLight);

        var directionalLight = new DirectionalLight(0xffffff, 0.9);
        directionalLight.position.set(200, 150, 130);
        directionalLight.lookAt(0, 0, 0);
        this.scene.add(directionalLight);

        this.particles = new Particles(window.innerWidth, window.innerHeight, { scale: 110 });
        this.particles.position.set(0, 0, 0);
        this.scene.add(this.particles);

        this.timeline = new Timeline();
       
        this.timeline.position.y = 5;
        this.timeline.position.z = 10;
        this.scene.add(this.timeline);

        window.addEventListener('mousemove', (e) => {
            this.particles.onMouseMove(e);
        });
    }

    setConfig(config: TimelineSectionConfig) {
        this.timeline.setConfig(config.timeline || {});
    }

    public updateCameraProjection(width: number, height: number) {
        this.camera.left = - width / 2;
        this.camera.right = width / 2;
        this.camera.top = height / 2;
        this.camera.bottom = -height / 2;
        this.camera.updateProjectionMatrix();
        this.rendererCSS.setSize(width, height);
    }

    private animateParicles(delta: number, elapsedTime: number) {
        this.particles.onUpdate(delta, elapsedTime, this.progress);
        this.particles.points.lookAt(this.camera.position);
    }

    onUpdate(delta: number, elapsedTime: number) {
        this.timeline.setProgress(this.innerProgress / (this._sectionHeight - window.innerHeight));
        this.timeline.onUpdate();
        this.rendererCSS.render(this.scene,this.camera);
        this.animateParicles(delta, elapsedTime);
    }

    get sectionHeight() {
        return this._sectionHeight;
    }

    setSectionHeight(value: number) {
        this._sectionHeight = value;
    }

    setSectionProgress(progress: number) {
        this.progress = progress;
        this.innerProgress = progress;
        this.backgroundSpec.mix = progress / this._sectionHeight;
    }
}
