import { Clock, Group, Object3D } from 'three';
import { createPathBuilder, PathData } from "./pathBuilder";
import tmp from '../../data/timeline';

interface TimelineNode {
    data: PathData;
    obj: THREE.Object3D;
}

export interface TimelineConfig {
    pathScale?: number;
}

export class Timeline extends Object3D {
    private pathGroup: Group = new Group();
    private clock: Clock;
    

    
    private pathScale: number = 1;
    private progress: number = 1;
    private maxX = 0;
    private downPosition: number = -40;
    private data: TimelineNode[] = [];
    constructor() {
        super();
       
        (async () => {
            let objPathBuilder = await createPathBuilder();
            this.pathGroup.add(objPathBuilder.getStart(-1, 1));
            this.data = tmp.map((item) => {
                const obj = objPathBuilder.getObject3d(item);
                obj.position.y = this.downPosition;
                this.pathGroup.add(obj);

                if (this.maxX < obj.position.x) this.maxX = obj.position.x;

                return { data: item, obj };
            })
        })();
        this.add(this.pathGroup);
        this.clock = new Clock();
    }

    setConfig(config: TimelineConfig) {
        this.pathScale = config.pathScale || this.pathScale;
        this.scale.setScalar(this.pathScale);
    }

    setProgress(progress: number){
        
        this.progress = progress*0.9;
    }

    onUpdate() {
        var delta = this.clock.getDelta();
        var speed =  50;
        this.position.x = -this.progress * this.maxX ;
        this.data.forEach((item) => {
            console.log(this.progress * this.maxX + 40 , item.obj.position.x);
            if (this.progress * this.maxX + 40 >= item.obj.position.x) {
                if (item.obj.position.y < 0) {
                    item.obj.position.y += speed * delta;
                } else {
                    item.obj.position.y = 0;
                }
            } else {
                if (item.obj.position.y > this.downPosition) {
                    item.obj.position.y -= speed * delta;
                } else {
                    item.obj.position.y = this.downPosition;
                }
            }
        })
        
    }
}
