import { useEffect, useState, MutableRefObject, useCallback } from 'react';

export function useAnimationFrame(callback: () => void) {
    useEffect(() => {
        let handle = 0;
        const runner = () => {
            callback();
            handle = requestAnimationFrame(runner);
        };

        runner();

        return () => cancelAnimationFrame(handle);
    }, [callback]);
}

export function useScrollTop(containerRef: MutableRefObject<HTMLElement | undefined>) {
    const [scrollTop, setScrollTop] = useState(0);

    useAnimationFrame(useCallback(() => {
        setScrollTop(containerRef.current?.scrollTop || 0);
    }, [containerRef, setScrollTop]));

    return scrollTop;
}

export function useWindowSize() {
    const [windowWidth, setWindowWidth] = useState<number>(window.innerWidth);
    const [windowHeight, setWindowHeight] = useState<number>(window.innerHeight);

    useEffect(() => {
        const handler = () => {
            setWindowWidth(window.innerWidth);
            setWindowHeight(window.innerHeight);
        };

        window.addEventListener('resize', handler, false);

        return () => window.removeEventListener('resize', handler, false);
    }, [windowWidth, windowHeight]);

    return [windowWidth, windowHeight];
}
