import './App.scss';

import React, {
    useState,
    useEffect,
    useRef,
} from 'react';
import {
    Switch,
    Route,
    useRouteMatch,
    useLocation,
} from 'react-router-dom';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import SimpleBar from 'simplebar-react';
import 'simplebar/dist/simplebar.min.css';

import { Header } from './common/header/Header';
import { HomeView } from './home/HomeView';
import { ContactView } from './contact/ContactView';
import { classNames } from '../utils/classNames';
import { useScrollTop } from '../utils/hooks';
import { PageKey, solarContext } from '../3d/solarContext';
import { ThreeContextProvider } from './abstract/ThreeContextProvider';
import { SiteLoader } from './siteLoader/SiteLoader';

export function useMockProgressCounter() {
    let [ progress, setProgress ] = useState(0);

    useEffect(() => {
        let timeout = -1;
        
        if (progress < 1) {
            let newProgress = Math.min(progress + 0.1 + Math.random() * 0.05, 1);
            timeout = window.setTimeout(() => setProgress(newProgress), 100 + Math.floor(Math.random() * 300));
        }

        return () => window.clearTimeout(timeout);
    }, [ progress ]);

    return progress;
}

enum PageSwitchDirection {
    LEFT,
    RIGHT,
}

type RouteMatch = {
    route: PageKey,
    matched: boolean,
};

export function useRoutes(): RouteMatch[] {
    return Object.values(PageKey).map(r => ({
        route: r,
        matched: (() => {
            // eslint-disable-next-line react-hooks/rules-of-hooks
            const match = useRouteMatch(r);
            return (match && match.isExact) === true;
        })()
    }));
}

export function usePageSwitchingDirection(routeMatches: RouteMatch[]): PageSwitchDirection {
    const currentPageIndexRef = useRef<number|null>(null);
    const [ switchDirection, setSwitchDirection ] = useState(PageSwitchDirection.LEFT);

    useEffect(() => {
        const currentRouteIndex = routeMatches.findIndex(r => r.matched);

        if (currentPageIndexRef.current !== currentRouteIndex) {
            if (currentPageIndexRef.current !== null) {
                setSwitchDirection(currentRouteIndex > currentPageIndexRef.current ? PageSwitchDirection.RIGHT : PageSwitchDirection.LEFT);
            }

            currentPageIndexRef.current = currentRouteIndex;
        }
    });

    return switchDirection;
}

export function App() {
    const routeMatches = useRoutes();
    const pageSwitchDirection = usePageSwitchingDirection(routeMatches);

    const currentRoute = routeMatches.find(r => r.matched)?.route || null;
    useEffect(() => {
        if (currentRoute !== null) {
            solarContext.setPage(currentRoute);
        }
    }, [ currentRoute ]);

    const scrollableNodeRef = useRef<HTMLElement>();
    const scrollTop = useScrollTop(scrollableNodeRef);
    const progress = useMockProgressCounter();

    const location = useLocation();

    return (
        <ThreeContextProvider value={solarContext}>
            <div className="App">
                <Header scrollTop={scrollTop} />
                <SiteLoader progress={progress} />
                <SimpleBar style={{ maxHeight: '100vh' }} scrollableNodeProps={{ ref: scrollableNodeRef }}>
                    <main className={classNames({
                        'App__main': true,
                        'App__main--page-switch-left': pageSwitchDirection === PageSwitchDirection.LEFT,
                    })}>
                        <section className="App__page-content">
                            <TransitionGroup>
                                <CSSTransition
                                    key={location.pathname}
                                    timeout={{
                                        enter: 300,
                                        exit: 300,
                                    }}
                                    classNames={'router-fade'}
                                    onEntered={() => {
                                        scrollableNodeRef.current?.scrollTo(0, 0);
                                    }}
                                >
                                        <Switch>
                                            <Route exact path="/">
                                                <HomeView scrollTop={scrollTop} />
                                            </Route>
                                            <Route path="/contact">
                                                <ContactView />
                                            </Route>
                                        </Switch>
                                </CSSTransition>
                            </TransitionGroup>
                        </section>
                    </main>
                </SimpleBar>
            </div>
        </ThreeContextProvider>
    );
}
