import { Object3D, Vector2, Vector3 } from "three";
import { Easing } from "../../utils/easing";
import { SmoothScalar } from "../../utils/smoothScalar";
import { worldToLocalMouse } from "../../utils/threeHelpers";
import Bubble, { IBubbleCollisionInfo } from "./Bubble";

export interface BubblesConfig {
    active?: boolean;
    visible?: boolean;
}

export default class Bubbles extends Object3D {
    private active: boolean;
    public items: Bubble[] = new Array<Bubble>();

    private _hoveredItem?: Bubble;

    private get hoveredItem() {
        return this._hoveredItem;
    }

    private set hoveredItem(value: Bubble | undefined) {
        if (this._hoveredItem) {
            this._hoveredItem.onMouseLeave();
        }

        this._hoveredItem = value;
        this._hoveredItem?.onMouseEnter();

        if (this._hoveredItem) {
            this.setCursor("pointer");
        } else {
            this.setCursor("default");
        }
    }

    private smoothScale = new SmoothScalar(0, 300, Easing.EASE_IN_OUT_SINE);
    private lastScaleValue = 0;
    private forceMultipler = 0.1;
    private forcePoint = new Vector2(0, 0);

    constructor(active: boolean = false) {
        super();
        this.init()
        let scale = 0.001;
        this.scale.set(scale, scale, scale);
        this.active = active;
    }

    private init() {
        this.generateBubbles();
        setTimeout(() => {
            this.fun();
        }, 6000)

    }

    private fun() {
        if (this.forceMultipler === 0.2) {
            this.forceMultipler = -0.5;
            this.forcePoint = new Vector2(Math.random() * 200 - 100, 0);
            setTimeout(() => {
                this.fun();
            }, 1000)
        } else {
            this.forceMultipler = 0.2;
            this.forcePoint = new Vector2(0, 0);
            setTimeout(() => {
                this.fun();
            }, 6000)
        }
    }


    private generateBubbles() {
        function getUrl(img: string) {
            return `${process.env.PUBLIC_URL}/sponsors/${img}`
        }
        let sponsors = [
            [getUrl("se.png"), "https://strefaenergii.com/"],
            [getUrl("agh.jpg"), "https://www.agh.edu.pl/"],
            [getUrl("weip.jpg"), "http://weip.agh.edu.pl/index.html"],
            [getUrl("tenzi.png"), "https://www.tenzi.pl/"],
            [getUrl("grm.png"), "http://www.grm-systems.cz/"],
            [getUrl("havel.png"), "http://www.havel-composites.pl/", 0xd9dbda],
            [getUrl("ksaf.jpg"), "https://www.ksaf.pl/", 0x040404],
            [getUrl("dracula.png"), "https://www.pizzeriadracula.pl/"],
            [getUrl("dancake.png"), "http://dancake.pl/"],
            [getUrl("anga.jpg"), "https://www.anga.com.pl/"],
            [getUrl("mag.svg"), "https://www.milmag.pl/"],
            [getUrl("fagh.png"), "https://www.fundacja.agh.edu.pl/"],
            [getUrl("dron.jpg"), "https://dron.edu.pl/", 0x000000],
            [getUrl("ansys.png"), "https://www.ansys.com/"],
            [getUrl("symkom.jpg"), "http://symkom.pl/"],
            [getUrl("eko.jpg"), "http://www.eko-energia.agh.edu.pl/"],
            [getUrl("3d.png"), "https://www.facebook.com/3DWayKrakow/", 0x212121],
        ]

        for (let sponsor of sponsors) {
            let radius = 30;
            if (sponsor[1] === "https://strefaenergii.com/") {
                radius = 50;
            }
            let position = new Vector2(Math.random() * 800 - 400, Math.random() * 800 - 400);
            let offset = position.clone().normalize().multiplyScalar(400);
            position.add(offset);
            // let color = Math.floor(Math.random() * 20 - 10) * 0x030303 + 0x292d2e;
            let color = (sponsor[2] !== undefined ? sponsor[2] : 0xffffff) as number;
            let linkUrl = sponsor[1] as string
            let logoUrl = sponsor[0] as string
            this.addBubble(new Bubble({ radius, logoUrl, position, color, linkUrl }));
        }

    }


    addBubble(bubble: Bubble) {
        this.add(bubble);
        this.items.push(bubble);
    }

    onUpdate(delta: number) {
        this.smoothScale.update();
        if (this.lastScaleValue !== this.smoothScale.value) {
            this.items.forEach((bubble) => bubble.scale.set(this.smoothScale.value, this.smoothScale.value, this.smoothScale.value));
            this.lastScaleValue = this.smoothScale.value;
        }

        if (!this.active) return;

        this.calculateBubblesMove(delta);

        this.rotateZ(0.01);
    }

    private calculateBubblesMove(delta: number) {



        this.items.forEach((bubble) => bubble.resetForce());

        // this.items.forEach((bubble1, index1) => {
        //     this.items.forEach((bubble2, index2) => {
        //         if (index1 < index2) { // To not do same pair twice
        //             let dx = bubble2.position.x - bubble1.position.x;  // Compute distance between centers of objects
        //             let dy = bubble2.position.y - bubble1.position.y;
        //             let r = Math.sqrt(Math.pow(dx, 2) + Math.pow(dy, 2));
        //             if (r < 10) {  // To avoid division by 0
        //                 r = 10;
        //             }
        //             // Compute force for this pair; k = 1000
        //             let f = (this.K * bubble1.mass * bubble2.mass) / Math.pow(r, 2);
        //             let fx = f* dx / r;  // Break it down into components
        //             let fy = f * dy / r;
        //            bubble1.force.x += fx;  // Accumulate for first object
        //             bubble1.force.y += fy;
        //             bubble2.force.x -= fx;  // And for second object in opposite direction
        //             bubble2.force.y -= fy;
        //         }
        //     })
        // });

        this.items.forEach((bubble) => {
            this.attractToPoint(bubble, this.forcePoint, 200, this.forceMultipler);
            bubble.update(delta);
        });

        this.items.forEach((bubble1, index1) => {
            this.items.forEach((bubble2, index2) => {
                if (index1 < index2) { // To not do same pair twice
                    let result = bubble1.checkCollision(bubble2);
                    if (result.collided && result.collisionInfo)
                        this.resolveCollisionWithBounce(result.collisionInfo);
                }
            })
        });

    }

    private attractToPoint(bubble: Bubble, point: Vector2, radius: number, forceMultipler: number) {
        let dx = point.x - bubble.position.x;
        let dy = point.y - bubble.position.y;
        let r = Math.sqrt(Math.pow(dx, 2) + Math.pow(dy, 2));

        if (r < radius) {
            bubble.speed.multiplyScalar(0.98);
        }

        if (r > radius && forceMultipler < 0) {
            forceMultipler *= -0.1;
        }


        let f = bubble.mass * r * forceMultipler;
        let fx = f * dx / r;
        let fy = f * dy / r;
        bubble.force.x += fx;
        bubble.force.y += fy;
    }


    private resolveCollisionWithBounce(info: IBubbleCollisionInfo) {
        let nx = info.dx / info.d;
        let ny = info.dy / info.d;
        let s = info.bubble1.radius + info.bubble2.radius - info.d;
        info.bubble1.position.x -= nx * s / 2;
        info.bubble1.position.y -= ny * s / 2;
        info.bubble2.position.x += nx * s / 2;
        info.bubble2.position.y += ny * s / 2;

        // let k = -2 *
        //     (
        //         (info.bubble2.speed.x - info.bubble1.speed.x) * nx +
        //         (info.bubble2.speed.y - info.bubble1.speed.y) * ny
        //     )
        //     / (1 / info.bubble1.mass + 1 / info.bubble2.mass);

        // info.bubble1.speed.x -= k * nx / info.bubble2.mass;
        // info.bubble1.speed.y -= k * ny / info.bubble2.mass;
        // info.bubble2.speed.x += k * nx / info.bubble1.mass;
        // info.bubble2.speed.y += k * ny / info.bubble1.mass;

        // let impulse = 2 * info.d / (info.bubble2.mass + info.bubble1.mass);
        // info.bubble1.speed.x -= (impulse * info.bubble2.mass * nx);
        // info.bubble1.speed.y -= (impulse * info.bubble2.mass * ny);
        // info.bubble2.speed.x += (impulse * info.bubble1.mass * nx);
        // info.bubble2.speed.y += (impulse * info.bubble1.mass * ny);
    }

    setConfig(config: BubblesConfig) {
        if (config.active) {
            this.activate();
        }
        else {
            this.deactivate();
        }
    }

    onMouseMove(e: MouseEvent, unprojectedMouse: Vector3) {
        if (!this.active) return;
        let found = undefined;
        let pos = worldToLocalMouse(unprojectedMouse, this);

        for (let bubble of this.items) {
            if (bubble.radius >= pos.distanceTo(bubble.position)) {
                found = bubble;
            }
        }

        this.hoveredItem = found;
    }

    onClick(e: MouseEvent) {
        if (!this.active) return;
        if (this.hoveredItem)
            this.hoveredItem.onClick();
    }

    deactivate() {
        this.active = false;
        this.hoveredItem = undefined;
        this.smoothScale.moveTo(0);
    }

    activate() {
        this.active = true;
        this.smoothScale.moveTo(1);
    }

    setCursor(cursor: string) {
        document.body.style.cursor = cursor;
    }
}