import { CircleGeometry, Color, DoubleSide, Mesh, MeshBasicMaterial, Object3D, Sprite, SpriteMaterial, TextureLoader, Vector2 } from "three";


interface IBubble {
    logoUrl?: string;
    linkUrl?: string;
    radius?: number; // m
    speed?: Vector2; // m/s
    acceleration?: Vector2; // m/s^2
    force?: Vector2; // m/s^2 * kg
    mass?: number; // kg
    position?: Vector2;
    color?: string | number | Color | undefined;
}

export interface IBubbleCollisionInfo {
    bubble1: Bubble;
    bubble2: Bubble;
    dx: number;
    dy: number;
    d: number;
}

export interface IBubbleCollisionResult {
    collisionInfo?: IBubbleCollisionInfo,
    collided: boolean
}

export default class Bubble extends Object3D {
    logoUrl: string;
    linkUrl: string;
    color: string | number | Color | undefined;

    radius: number; // m
    speed: Vector2; // m/s
    acceleration: Vector2; // m/s^2
    force: Vector2; // m/s^2 * kg
    mass: number; // kg

    private material?: MeshBasicMaterial;
    private outlineMaterial?: MeshBasicMaterial;
    private geometry?: CircleGeometry;

    constructor(params: IBubble = {} as IBubble) {
        super();

        let {
            logoUrl = "",
            linkUrl = "",
            radius = 50,
            speed = new Vector2(0, 0),
            acceleration = new Vector2(0, 0),
            force = new Vector2(0, 0),
            mass = Math.PI * 2 * radius,
            position = new Vector2(0, 0),
            color = 0x292d2e,
        } = params;

        this.logoUrl = logoUrl;
        this.linkUrl = linkUrl;
        this.radius = radius;
        this.speed = speed;
        this.acceleration = acceleration;
        this.force = force;
        this.mass = mass;
        this.position.x = position.x;
        this.position.y = position.y;
        this.color = color;

        this.initShape();
    }

    private initShape() {
        this.geometry = new CircleGeometry(this.radius, 32);
        this.material = new MeshBasicMaterial({ color: this.color, side: DoubleSide });
        this.outlineMaterial = new MeshBasicMaterial({ color: 0xf16f01, side: DoubleSide, transparent: true, opacity: 0 });

        var circle = new Mesh(this.geometry, this.material);
        this.add(circle);

        var outline = new Mesh(this.geometry, this.outlineMaterial);
        outline.scale.set(1.05, 1.05, 1.05);
        outline.position.z = -1;
        this.add(outline);

        var texture = new TextureLoader().load(this.logoUrl, (tex) => {

            var material2 = new SpriteMaterial({ map: texture });
            var sprite = new Sprite(material2);
            let x = 0, y = 0, factor = 0;
            let maxSize = this.radius * Math.sqrt(2) * 0.95;

            if (tex.image.width > tex.image.height) {
                x = maxSize;
                factor = tex.image.width / x;
                y = tex.image.height / factor;
            } else {
                y = maxSize;
                factor = tex.image.height / y;
                x = tex.image.width / factor;
            }

            sprite.scale.set(x, y, 1);
            this.add(sprite);
        });
    }

    update(delta: number) {
        delta = delta < 1 / 20 ? delta : 1 / 20;
        this.calculateAcceleration();
        this.calculateMove(delta);
    }

    private calculateAcceleration() {
        this.acceleration.x = this.force.x / this.mass;
        this.acceleration.y = this.force.y / this.mass;
    }

    private calculateMove(delta: number) {
        this.speed.x += this.acceleration.x * delta;
        this.speed.y += this.acceleration.y * delta;
        this.position.x += this.speed.x * delta;
        this.position.y += this.speed.y * delta;
    }

    resetForce() {
        this.force.x = 0;
        this.force.y = 0;
    }

    checkCollision(bubble: Bubble): IBubbleCollisionResult {
        let dx = bubble.position.x - this.position.x;
        let dy = bubble.position.y - this.position.y;
        let d = Math.sqrt(Math.pow(dx, 2) + Math.pow(dy, 2));
        if (d < this.radius + bubble.radius) {
            let collisionInfo: IBubbleCollisionInfo = { bubble1: this, bubble2: bubble, dx, dy, d };
            return {
                collisionInfo: collisionInfo,
                collided: true
            }
        }
        return {
            collisionInfo: undefined,
            collided: false
        }
    }

    onClick() {
        if (this.linkUrl !== "")
            window.open(this.linkUrl, "_blank");
    }

    onMouseEnter() {
        this.outlineMaterial?.setValues({ opacity: 1 });
        this.position.z = 1;
    }

    onMouseLeave() {
        this.outlineMaterial?.setValues({ opacity: 0 });
        this.position.z = 0;
    }
}