import React from 'react';
import classNames from 'classnames';
import './SideStoryPanel.scss';

export interface SideStoryPanelProps {
    title: string;
    visible: boolean;
    marginTop?: number;
    marginBottom?: number;
    children: React.ReactNode;
}

export function SideStoryPanel(props: SideStoryPanelProps) {
    return (
        <div className={classNames('SideStoryPanel', {
                visible: props.visible,
            })}
            style={{
                paddingTop: `${props.marginTop || 0}px`,
                paddingBottom: `${props.marginBottom || 0}px`,
            }}
        >
            <div className="SideStoryPanel--panel">
                <h1 className="SideStoryPanel--title">{props.title}</h1>
                <div className="SideStoryPanel--description">
                    {props.children}
                </div>
            </div>
        </div>
    );
}
