export function getOpacityForStop(stops: number[], index: number, progress: number) {
    const startTime = stops[index - 1];
    const peakTime = stops[index];
    const endTime = stops[index + 1];

    if (startTime !== undefined && progress <= startTime) {
        return 0;
    }
    else if (progress < peakTime) {
        return (progress - startTime) / (peakTime - startTime);
    }
    else if (endTime !== undefined && progress <= endTime) {
        return 1 - (progress - peakTime) / (endTime - peakTime);
    }
    else if (endTime === undefined) {
        return 1;
    }

    return 0;
};

export function getColorFromStops(stops: { progress: number, color: number[] }[], progress: number): string {
    for (let i = 0; i < stops.length - 1; ++i) {
        const prevStop = stops[i];
        const nextStop = stops[i + 1];

        if (progress <= nextStop.progress) {
            const t = (progress - prevStop.progress) / (nextStop.progress - prevStop.progress);
            const mix = (color0: number, color1: number, t: number) => Math.floor((color0 + (color1 - color0) * t) * 255);
            return `rgb(${mix(prevStop.color[0], nextStop.color[0], t)}, ${mix(prevStop.color[1], nextStop.color[1], t)}, ${mix(prevStop.color[2], nextStop.color[2], t)})`;
        }
    }

    return '';
};
