import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter } from 'react-router-dom';
import './index.css';
import { App } from './components/App';
import * as serviceWorker from './serviceWorker';
import { solarContext, HomeSectionKey, PageKey, SimpleSectionKey } from './3d/solarContext';
import { PlaneSection } from './3d/plane/planeSection';
import { TimelineSection } from './3d/timeline/timelineSection';
import { SimpleSection } from './3d/section/simpleSection';

// Setting up the Three.JS 3d world.
solarContext.init({
    [PageKey.HOME]: {
        sections: [
            new PlaneSection(HomeSectionKey.PLANE_SECTION),
            new TimelineSection(HomeSectionKey.TIMELINE_SECTION),
        ]
    },
    [PageKey.PLANES]: {
        sections: [
            new SimpleSection(SimpleSectionKey.MAIN_SECTION, {
                primaryIndex: 4,
                secondaryIndex: 0,
                mix: 0,
            }),
        ]
    },
    [PageKey.TEAM]: {
        sections: [
            new SimpleSection(SimpleSectionKey.MAIN_SECTION, {
                primaryIndex: 1,
                secondaryIndex: 0,
                mix: 0,
            }),
        ]
    },
    [PageKey.CONTACT]: {
        sections: [
            new SimpleSection(SimpleSectionKey.MAIN_SECTION, {
                primaryIndex: 2,
                secondaryIndex: 0,
                mix: 0,
            }),
        ]
    },
});

ReactDOM.render(
    <React.StrictMode>
        <BrowserRouter>
            <App />
        </BrowserRouter>
    </React.StrictMode>,
    document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
